/* stylelint-disable at-rule-empty-line-before,at-rule-name-space-after,at-rule-no-unknown */
.ripple-btn {
  position: relative;
  display: inline-block;
  padding: 8px 16px;
  font-size: 14px;
  min-width: 64px;
  min-height: 36px;
  box-sizing: border-box;
  line-height: 1.5;
  border-radius: 4px;
  font-weight: 500;
  overflow: hidden;
  transition: background-color 250ms;
  border: none;
  color: #282a3c;
  background: transparent;
}
.ripple-btn > span {
  user-select: none;
  position: relative;
  z-index: 1;
}
.ripple-btn > .ripple-animate {
  position: absolute;
  z-index: 0;
  border-radius: 50%;
  transition: transform 700ms, opacity 700ms;
  transition-timing-function: cubic-bezier(0.25, 0.46, 0.45, 0.94);
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100%;
  transform: scale(0);
  pointer-events: none;
}
.ripple-btn:not(.ghost) {
  color: #282a3c;
  background: #f3f6f9;
}
.ripple-btn:not(.ghost) > .ripple-animate {
  background: rgba(40, 42, 60, 0.4);
}
.ripple-btn:not(.ghost).primary {
  color: #ffffff;
  background: #5d78ff;
}
.ripple-btn:not(.ghost).primary > .ripple-animate {
  background: rgba(255, 255, 255, 0.4);
}
.ripple-btn:not(.ghost).danger {
  color: #ffffff;
  background: #fd397a;
}
.ripple-btn:not(.ghost).danger > .ripple-animate {
  background: rgba(255, 255, 255, 0.4);
}
.ripple-btn.ghost {
  color: #282a3c;
  background: transparent;
}
.ripple-btn.ghost > .ripple-animate {
  background: rgba(40, 42, 60, 0.4);
}
.ripple-btn.ghost:hover,
.ripple-btn.ghost:active {
  background: rgba(40, 42, 60, 0.08);
}
.ripple-btn.ghost.primary {
  color: #5d78ff;
  background: transparent;
}
.ripple-btn.ghost.primary:hover,
.ripple-btn.ghost.primary:active {
  background: rgba(93, 120, 255, 0.2);
}
.ripple-btn.ghost.primary > .ripple-animate {
  background: rgba(93, 120, 255, 0.4);
}
.ripple-btn.ghost.danger {
  color: #fd397a;
  background: transparent;
}
.ripple-btn.ghost.danger:hover,
.ripple-btn.ghost.danger:active {
  background: rgba(253, 57, 122, 0.2);
}
.ripple-btn.ghost.danger > .ripple-animate {
  background: rgba(253, 57, 122, 0.4);
}
